<template>
  <div  class="sourceselection" >
  <div class="jumbotron">
    <h2><span class="glyphicon glyphicon-list-alt"> </span> News list</h2>
  <h4> Select News source</h4>
  <select class="form-control" v-on:change="sourceChanged">
    <option value="">Please select news source ...</option>
    <option v-bind:value="source.id" v-for="source in sources">{{source.name}}</option>

  </select>
     <div v-if="source">
          <h5>Description: {{source.description}}</h5>
          <h6>Category: {{source.category}}</h6>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'sourceselection',
  data () {
    return{
        sources:[],
        source: ''
    }
  },
  methods:{
   sourceChanged: function(e){
    for(var i=0; i<this.sources.length;i++){
     if(this.sources[i].id==e.target.value){
      this.source=this.sources[i];
      }
    }
    this.$emit('sourceChanged',e.target.value);
   
   }
  },
  created:function(){
   this.$http.get('https://newsapi.org/v1/sources?language=en').then(response =>{
   this.sources=response.data.sources;
   });
  }
}
  

</script>

<style scoped>
 .sourceselection{
    padding-top:20px;
 }
</style>



