<template>
  <div  class="newslist" >
  <div class="container">
   <ul class="media-list">
         <li class="media" v-for="article in articles">
         <div class="media-left">
             <a v-bind:href="article.url" target ='_blank'>
             <img class="media-object" :src="article.urlToImage">
             </a>

         </div>
         <div class="media-body">
             <h4 class="media-heading">
              <a v-bind:href="article.url" target="_blank">{{article.title}}</a>
             </h4>
             <h5>by {{ article.author}}</h5>
             <p> {{ article.description }}</p>
             <h5>Published at : {{ article.publishedAt}}</h5>
         </div>
         </li>
   </ul>

  </div>

  </div>
</template>

<script>
export default {
  name: 'newslist',
  props:['source'],
  data (){
    return{
     articles:[]
    }
  },
  methods:{
   updateSource: function(source){
   if(source!=''){
   this.$http.get('https://newsapi.org/v1/articles?source='+source+'&sortBy=top&apiKey=b2a1efe7109346e981e33a9517f780c4').then( response=>{
    this.articles= response.data.articles;
   	})}
   }
  },
  created:function(){
   this.updateSource(this.source);
  },
  watch:{
    source:function(val)
    {
       this.updateSource(val);
    }
  }
}
  

</script>

<style scoped>
 .newslist{
 padding-top:20px;
 }
   .media-object {
    width: 128px;
    padding: 10px;
  }
  .media {
     background-color:#eee;;

    border-top: 1px solid lightgray;
    padding-top: 20px;
  }
</style>



